<template lang="html">
  <div class="">
    <default-head header="评价订单" :hasArrow="false"></default-head>
    <div id="wrapper">
      <search-list :isShow="false" :paddingB="0" :data="searchItem"></search-list>
      <div class="appraise-box">
        <div class="appraise-notice">
          <span class="appraise-notice__text">用户评价: </span>
          <span class="appraise-notice__msg">提交用户评价可查看商家素颜照</span>
        </div>
        <flm-star @star-change="changeStar"></flm-star>
        <div class="appraise-foot-box">
          <input class="appraise-foot__btn appraise-foot__btn--left"
          type="button" name="" value="提交评价" @click="commitComment()">
          <input class="appraise-foot__btn appraise-foot__btn--right"
          type="button" name="" value="下次再评" @click="next()">
        </div>
      </div>
    </div>
  </div>

</template>

<script>
import api from '@/api'
import utils from '@/utils/utils'
import minutils from '@/utils/minUtils'
import defaultHead from '@/components/head/defaultHead'
import searchList from '@/components/appraiseOrder/searchList'

export default {
  data () {
    return {
      searchItem: [],
      starNum: 0
    }
  },
  computed: {
    searchId: function () {
      return sessionStorage.getItem('searchId')
    },
    commentId: function () {
      return sessionStorage.getItem('commentId')
    },
    searchType: function () {
      return sessionStorage.getItem('searchType')
    },
    userId: function () {
      return utils.userId()
    }
  },
  mounted () {
    this.getUserDetail(this.userId, this.searchId)
  },
  components: {defaultHead, searchList},
  methods: {
    next () {
      window.history.go(-1)
    },
    changeStar (num) {
      this.starNum = num
    },
    getUserDetail (loginId, userId) {
      api.getUserDetail({loginId: loginId, userId: userId}).then((response) => {
        let result = response.body
        if (result.isSuccess) {
          this.searchItem = [result.data]
        }
      })
    },
    commitComment () {
      if (!this.userId) {
        this.$router.push('/login')
        return
      }
      if (!this.starNum) {
        minutils.Toast({
          message: '评价至少一颗星',
          duration: 1000})
        return
      }
      api.saveUserComment({userId: this.userId, id: this.commentId, score: this.starNum}).then((response) => {
        let result = response.body
        if (result.isSuccess) {
          this.$router.push('/detail')
        }
      })
    }
  }
}
</script>

<style lang="less">
@import '../assets/css/variable.less';

.appraise-box {
  background: @whiteColor;
  padding-top: 1rem;
  text-align: center;
  font-size: .32rem;
  .appraise-notice {
    margin-bottom: .4rem;
  }
  .appraise-notice__text {
    color: @defaultColor;
  }
  .appraise-notice__msg {
    color: @lightColor;
  }
  .appraise-foot-box {
    padding: 1rem .22rem .4rem .22rem;
    overflow: hidden;
    .appraise-foot__btn {
      width: 3.32rem;
      height: .88rem;
      border-radius: .05rem;
      background:@whiteColor;
      border: 1px solid @lightColor;
      font-size: .32rem;
    }
    .appraise-foot__btn--left {
      float: left;
      background: @greenBtn;
      color: @whiteColor;
      border: 1px solid @greenBtn;
    }
    .appraise-foot__btn--right {
      float: right;
      color: @defaultColor;
    }
  }
}
</style>



