<template lang="html">

  <div class="search-list clearfix"
    v-infinite-scroll="loadMore"
    :infinite-scroll-disabled="loading"
    :infinite-scroll-distance="10"
    :infinite-scroll-immediate-check="true"
    :style="{paddingBottom: paddingB}"
  >
    <div v-for="item in data" class="search-list-item">
      <!--  @click="getListDetail(item)"-->
      <div class="detail-cover"></div>
      <div class="search-list-left" :class="{'woman': parseInt(item.userDetails.sex) === 2}">
        <img class="search-list-left__img" v-if="item.userDetails.userType" :src="item.userDetails.logo || zhanwt">
        <img v-else-if="parseInt(item.userDetails.userType) === 0 && parseInt(item.userDetails.sex) === 0" :src="this.defaultLogo" alt="">
        <span class="search-list-left__name" v-else="item.userDetails.realName">{{item.userDetails.realName | toFormatName}}</span>
        <span class="search-list-left__vip" :class="{'redColor': !!item.userDetails.isVip, 'borderBRed': !!item.userDetails.isVip}">VIP</span>
      </div>
      <!-- 中间灰线 -->
      <span class="search-list-line"></span>
      <!-- 右侧 -->
      <div class="search-list-right">
        <div class="search-list-right__top clearfix">
          <span class="search-list-right__number fl">{{item.userDetails.mobile | toFormatMobile}}</span>
          <span class="search-list-right__times fl"><i class="phone__icon"></i>{{item.commentTotal.callCount}}次</span>
          <span class="search-list-right__percent fl"><i class="zan-icon"></i>{{goodCommentRate}}</span>
        </div>
        <div class="search-list-right__middle">
          <span class="search-list-right__address" v-if="item.userDetails.mobilePlace">{{item.userDetails.mobilePlace}}</span>
          <span class="search-list-right__address" v-else>{{item.address.province}} {{item.address.city}}</span>
          <i class="arrow__icon"  v-if="isShow"></i>
        </div>
        <div class="search-list-right__bottom clearfix">
          <span class="search-list-right__info fl">{{item.userTags[0] && item.userTags[0].name}}</span>
          <!-- <span class="search-list-right__range fr" v-if="isShow">{{item.distance}}m</span> -->
        </div>
      </div>
    </div>
  </div>

</template>

<script>
import _ from 'underscore'
import hzl from '@/assets/img/hzl@2x.png'
import logo from '@/assets/img/logo.png'
export default {
  props: {
    data: {
      type: Array,
      default: [
        {
          commentTotal: {},
          userTags: [],
          userDetails: {}
        }
      ]
    },
    isShow: {
      type: Boolean,
      default: true
    },
    paddingB: {
      type: String | Number,
      default: '1.12rem'
    }
  },
  data () {
    return {
      loading: false,
      zhanwt: hzl,
      defaultLogo: logo
    }
  },
  computed: {
    goodCommentRate: function () {
      return parseFloat(this.data[0].commentTotal.goodCommentRate) + '%'
    }
  },
  mounted () {
    console.log(this.data)
  },
  methods: {
    getListDetail (searchId) {
      console.log(searchId)
      sessionStorage.setItem('searchId', searchId)
      this.$router.push('/detail')
    },
    loadMore: _.throttle(function () {
      // 关闭无线滚动
      this.loading = true
      this.$emit('load-more', this.changeLoading)
    }, 300),
    // 启动无线滚动
    changeLoading () {
      this.loading = false
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';
@import '../../assets/css/property.less';
.search-list {
  // padding-bottom: .3rem;
}
.search-list-item {
  width: 100%;
  height: 1.56rem;
  padding: .2rem 0 .2rem .22rem;
  position: relative;
  background: @whiteColor;
  border-bottom: 1px solid @lineColor;
  .detail-cover {
    position: absolute;
    right: 0;
    top: 0;
    z-index: 2;
    width: 1.6rem;
    height: 1.5rem;
    // background: rgba(1, 1, 1, .08);
  }
  .search-list-left {
    width: .8rem;
    height: 100%;
    float: left;
    .search-list-left__name {
      display: block;
      text-align: center;
      width: .6rem;
      height: .6rem;
      font-size: .32rem;
      line-height: .6rem;
      color: @whiteColor;
      background: @manColor;
      border-radius: 50%;
    }
    .search-list-left__img {
      display: block;
      width: .6rem;
      height: .6rem;
      background: tan;
      border-radius: 50%;
    }
    .search-list-left__vip {
      display: block;
      width: .6rem;
      text-align: center;
      padding-bottom: .04rem;
      margin-top: .14rem;
      font-size: .3rem;
      font-style: italic;
      color: @lightColor;
      border-bottom: 1px solid @lightColor;
    }
    .borderBRed {
      border-bottom: 1px solid @redColor !important;
    }
    &.woman {
      .search-list-left__name {
        background: @womanColor !important;
      }
      // .search-list-left__vip {
      //   color: @infoColor !important;
      //   border-color: @infoColor !important;
      // }
    }

  }
  .search-list-line {
    float: left;
    width: 1px;
    height: .94rem;
    margin-top: .11rem;
    background: @lineColor;
  }
  .search-list-right {
    // float: left;
    // width: 6.22rem;
    // padding-left: .2rem;
    margin-left: 1rem;
    .search-list-right__top {
      font-size: .28rem;
      // padding-right: .2rem;
      color: @defaultColor;
      height: .24rem;
      line-height: .24rem;
      .search-list-right__number {
        font-size: .32rem;
      }
      .search-list-right__times {
        width: 1rem;
        position: relative;
        color: @lightColor;
        margin-left: 1.3rem;
        .phone__icon {
          top: -.1rem;
          left:-.446rem;
        }
      }
      .search-list-right__percent {
        margin-left: .66rem;
        color: @lightColor;
        position: relative;
        .zan-icon {
          position: absolute;
          top: -.1rem;
          left:-.44rem;
          width: .44rem;
          height: .44rem;
          background: url(../../assets/img/zan@2x.png) no-repeat;
          background-size: .44rem .44rem;
        }
      }
    }
    .search-list-right__middle {
      width: 100%;
      font-size: .28rem;
      position: relative;
      height: .64rem;
      line-height: .64rem;
      .search-list-right__address {
        display: inline-block;
        width: 80%;
        height: .64rem;
        overflow: hidden;
        text-overflow: ellipsis;
        color: @lightColor;
      }
      .arrow__icon {
        right: .08rem;
        top: .1rem;
      }
    }
    .search-list-right__bottom {
      width: 100%;
      font-size: .28rem;
      padding-right: .42rem;
      height: .28rem;
      line-height: .28rem;
      .search-list-right__info {
        width: 4rem;
        height: .3rem;
        line-height: .32rem;
        overflow: hidden;
        color: @infoColor;
      }
      .search-list-right__range {
        color: @lightColor;
      }
    }
  }
}
</style>



